import React from "react";
import Slider from "react-slick";

const Featuredsection = () => {
  const categories = [
    { icon: "icon-skincare", title: "Beauty", products: 1 },
    { icon: "icon-food", title: "Grocery", products: 3 },
    { icon: "icon-medicine", title: "Pharmacy", products: 1 },
    { icon: "icon-mask-1", title: "Surgical Mask", products: 1 },
    { icon: "icon-medical", title: "Medkits", products: 2 },
    { icon: "icon-food", title: "Medicine", products: 1 },
    { icon: "icon-stethoscope", title: "Stethoscope", products: 1 },
  ];

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    autoplay: true,
    autoplaySpeed: 3000,
    slidesToShow: 6,
    slidesToScroll: 1,
    arrows: false,
    responsive: [
      { breakpoint: 1200, settings: { slidesToShow: 4 } },
      { breakpoint: 992, settings: { slidesToShow: 3 } },
      { breakpoint: 768, settings: { slidesToShow: 2 } },
      { breakpoint: 576, settings: { slidesToShow: 2 } },
      { breakpoint: 476, settings: { slidesToShow: 2 } },
    ],
  };

  return (
    <section className="featured-section style-3">
      <div className="container-fluid">
        <Slider {...settings}>
          {categories.map((cat, index) => (
            <div className="featured-item" key={index}>
              <div className="icon">
                <i className={cat.icon}></i>
              </div>
              <div className="text">
                <h2>
                  <a href="/shop">{cat.title}</a>
                </h2>
                <span>{cat.products} Product</span>
              </div>
            </div>
          ))}
        </Slider>
      </div>
    </section>
  );
};

export default Featuredsection;
